	module.exports = {"data":{"markdownRemark":{"html":"<p>よくゲームプログラム集などに載ってる数当てゲームをうまくバトルゲームに発展させたものです。</p>\n<p>ルールは大体想像がつくかと思いますが、簡単に説明するとランダムで出される 1～9 までの数を五ターン以内に当てて、少ないターン数で答えれば敵に大ダメージを与えることが出来ます。\n一定のダメージを敵に与えると敵を倒すことが出来、第二戦へと突入します。</p>\n<p>敵は戦闘を重ねるごとに強くなっていきますので一ターンごとに出るヒントを元に慎重に推理することが重要となってきます。</p>\n<p>このゲームの目玉は特定条件で出る<em>『運命のスロット』</em>と言う隠し技なのですが、その名の通り出目によって今後の戦闘の運命を左右される重要な要素です。\n例えば 2 でそろうと二回攻撃、7 なら超必殺、8 なら HP 全回復などありますが中にはプレイヤーの攻撃力半減、果ては<em>即ゲームオーバー</em>などとシビアな出目もあります。\nこの要素がどうやらウケたらしく、部活内でも結構広まってました(笑</p>\n<p>また、部活の顧問もこれを見て今までプログラムの効率的な組み方程度しか教えてくれなかったところを、初めてゲームとしての批評を下してくれました。\nこれが私のプログラムをいっぱしの『ゲームソフト』と見てくれたのか、それとも余りにクソゲーだったので見ていられなくなったのかは結局分かりませんでしたが。\n少なくとも自分の作品の中では、まともな部類ですｗ</p>\n<h2>Java移植版について</h2>\n<p>仕事上 Java のスキルが必要だったので勉強がてら移植したものです。\n完全な移植ではなく、ビジュアル要素や「運命のスロット」機能などはありません。</p>\n<ul>\n<li>ジャンル: クイズ</li>\n<li>開発言語(旧): N88-日本語BASIC(86) MS-DOS版</li>\n<li>開発言語(移植版): JavaSE 6</li>\n<li>最終更新: 2007/1/4</li>\n<li><a href=\"/filez/app/java/NumBattleGame.zip\">JAVA移植版をダウンロード</a> ([filesize]<a href=\"https://danmaq.com/filez/app/java/NumBattleGame.zip%5B/filesize%5D\">https://danmaq.com/filez/app/java/NumBattleGame.zip[/filesize]</a>)</li>\n</ul>","frontmatter":{"cover":null,"date":"1996-03-01T00:00:00+09:00","strDate":"1996/2/29","title":"数当てバトルゲーム"}}},"pathContext":{"path":"/ja/blog/1996/03/01-quiz-battle/","langKey":"ja"}}

